 -------------------------- */
.tu-tooltip:focus:not(.focusing), .tu-tooltip:focus:hover {
  outline-width: 0; }

.tu-tooltip__popper {
  position: absolute;
  border-radius: 2px;
  padding: 10px;
  z-index: 2000;
  font-size: 12px;
  line-height: 1.2;
  min-width: 10px;
  word-wrap: break-word; }
  .tu-tooltip__popper .popper__arrow,
  .tu-tooltip__popper .popper__arrow::after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid; }
  .tu-tooltip__popper .popper__arrow {
    border-width: 4px; }
  .tu-tooltip__popper .popper__arrow::after {
    content: " ";
    border-width: 5px; }
  .tu-tooltip__popper[x-placement^="top"] {
    margin-bottom: 10px; }
  .tu-tooltip__popper[x-placement^="top"] .popper__arrow {
    bottom: -4px;
    border-top-color: #9baacf;
    border-bottom-width: 0; }
    .tu-tooltip__popper[x-placement^="top"] .popper__arrow::after {
      bottom: 1px;
      margin-left: -5px;
      border-top-color: #bec8e4;
      border-bottom-width: 0; }
  .tu-tooltip__popper[x-placement^="bottom"] {
    margin-top: 10px; }
  .tu-tooltip__popper[x-placement^="bottom"] .popper__arrow {
    top: -4px;
    border-top-width: 0;
    border-bottom-color: #9baacf; }
    .tu-tooltip__popper[x-placement^="bottom"] .popper__arrow::after {
      top: 1px;
      margin-left: -5px;
      border-top-width: 0;
      border-bottom-color: #bec8e4; }
  .tu-tooltip__popper[x-placement^="right"] {
    margin-left: 10px; }
  .tu-tooltip__popper[x-placement^="right"] .popper__arrow {
    left: -4px;
    border-right-color: #9baacf;
    border-left-width: 0; }
    .tu-tooltip__popper[x-placement^="right"] .popper__arrow::after {
      bottom: -5px;
      left: 1px;
      border-right-color: #bec8e4;
      border-left-width: 0; }
  .tu-tooltip__popper[x-placement^="left"] {
    margin-right: 10px; }
  .tu-tooltip__popper[x-placement^="left"] .popper__arrow {
    right: -4px;
    border-right-width: 0;
    border-left-color: #9baacf; }
    .tu-tooltip__popper[x-placement^="left"] .popper__arrow::after {
      right: 1px;
      bottom: -5px;
      margin-left: -5px;
      border-right-width: 0;
      border-left-color: #bec8e4; }
  .tu-tooltip__popper.is-dark {
    background: #bec8e4;
    color: #ffffff; }
